.tournaments__container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  .tournaments__content {
    height: calc(100vh - 60px);
    padding: 20px 20px 0 20px;
    overflow: auto;

    .tournaments__plug {
      height: calc(100% - 150px);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    .tournaments__plug-title {
      text-align: center;
      margin: 10px;
    }

    .tournaments__plug-text {
      text-align: center;
      font-size: 18px;
      margin: 10px;
    }
  }

  .tournaments__creation{
    padding: 0 20px 80px;
  }
}

.filter__button-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0 0 0;
}

.filter__button {
  color: #fff;
  font-size: 17px;
  margin: 0;
  font-weight: 600;
  border: 1px solid #4694D9;
  border-radius: 11px;
  background-color: #4694D9;
  padding: 14px 0 14px 0;
  width: 45%;
}

.filter__button:first-child {
  background-color: #fff;
  color: #4694D9;
}

.tournaments__button-addition {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 20px;
  bottom: 80px;
  border-radius: 100%;
  width: 50px;
  height: 50px;
  background-color: #4694D9;
  cursor: pointer;
}

